import React, { useState, useEffect } from "react";
import axios from "axios";

import { IoLogoJavascript } from "react-icons/io";
import { DiPhp, DiCode } from "react-icons/di";
import { FaGithub, FaHtml5 } from "react-icons/fa";

function Repos() {
  const reposUrl = "https://api.github.com/users/rodolfosantos23/repos";
  const [repos, setRepos] = useState([]);

  useEffect(() => {
    axios.get(reposUrl).then((res) => {
      setRepos(res.data);
    });
  }, []);

  return (
    <section id="repos">
      <div className="container">
        <h2>Repositórios</h2>

        <p>
          Listagem dos meus repositórios no{" "}
          <a href="https://github.com/rodolfosantos23">GitHub</a>
        </p>
        <br></br>

        <div className="repo-list">
          {repos.map((repo) => {
            let logo = <DiCode />;
            if (repo.language === "PHP") {
              logo = <DiPhp />;
            }
            if (repo.language === "JavaScript") {
              logo = <IoLogoJavascript />;
            }
            if (repo.language === "HTML") {
              logo = <FaHtml5 />;
            }

            return (
              <div className="flex" key={repo.id}>
                <a
                  className="repo"
                  href={repo.html_url}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <h3>
                    <FaGithub /> {repo.name}
                  </h3>
                  <p>{repo.description}</p>
                  <br></br>
                  <small>
                    {logo} {repo.language}
                  </small>
                </a>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}

export default Repos;
