import React from "react";

import phpIcon from "./../../images/php-icon.png";
import reactIcon from "./../../images/react-icon.png";
import nodeIcon from "./../../images/node-icon.png";
import mongoIcon from "./../../images/mongo-icon.png";
import sqlIcon from "./../../images/sql-icon.png";
import tsIcon from "./../../images/ts-icon.png";

const Skills = () => {
  return (
    <section id="skills">
      <div className="container">
        <h2>Skills</h2>

        <div className="row">
          <div className="col-md-4">
            <div className="skill">
              <div className="icon">
                <img src={phpIcon} alt="PHP + Yii Framework" />
              </div>
              <div className="detail">
                <h3>PHP + Yii Framework</h3>
                <p>
                  Conhecimento avançado em PHP 7 e o Yii Framework. Ampla
                  experiência em desenvolvimento de sistemas e API's
                </p>
              </div>
            </div>
          </div>

          <div className="col-md-4">
            <div className="skill react">
              <div className="icon react">
                <img src={reactIcon} alt="ReactJS" />
              </div>
              <div className="detail">
                <h3>ReactJS</h3>
                <p>
                  Meu foco atual! Construção de Single Page Aplications,
                  Progressive Web Apps e frontends em real time
                </p>
              </div>
            </div>
          </div>

          <div className="col-md-4">
            <div className="skill">
              <div className="icon">
                <img src={nodeIcon} alt="NodeJS" />
              </div>
              <div className="detail">
                <h3>NodeJS</h3>
                <p>
                  Principal ferramenta que utilizo hoje para desenvolvimento do
                  backend e construção de API Rest
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-md-4">
            <div className="skill">
              <div className="icon">
                <img src={tsIcon} alt="JavaScript + TypeScript" />
              </div>
              <div className="detail">
                <h3>JavaScript + TypeScript</h3>
                <p>
                  Linguagem de programação utilizada no React e Node, aliado ao
                  TypeScript se tornando uma poderosa linguagem tipada
                </p>
              </div>
            </div>
          </div>

          <div className="col-md-4">
            <div className="skill">
              <div className="icon">
                <img src={sqlIcon} alt="SQL" />
              </div>
              <div className="detail">
                <h3>SQL</h3>
                <p>
                  Conhecimento em modelagem de dados e linguagem SQL para bancos
                  de dados: MySQL, SQL, MariaDB, PostgreSQL
                </p>
              </div>
            </div>
          </div>

          <div className="col-md-4">
            <div className="skill">
              <div className="icon">
                <img src={mongoIcon} alt="MongoDB" />
              </div>
              <div className="detail">
                <h3>MongoDB</h3>
                <p>
                  Desenvolvimento de bases em MongoDB focado em escalabilidade e
                  grande volume de dados
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skills;
